<template>
    <div class="index">
        <div class="content">
            <div class="con-info info-1">
                <div class="info-item">
                    <span>房间号</span>
                    <span>{{roomNumber}}</span>
                </div>
                <div class="info-item">
                    <span>安全码</span>
                    <span style="font-family: 宋体;">{{safetyCode}}</span>
                </div>
            </div>
            <div class="con-info info-2">
                <div class="info-item">
                    <div class="download-open-title">
                        <p>玩家APP</p>
                        <p>下载地址</p>
                    </div>
                    <div class="download-open">
                        <span @click="handleCopy(address.app)">复制</span>
                        <span @click="handleJump(address.app)">打开</span>
                    </div>
                </div>
                <div class="info-item">
                    <div class="download-open-title">
                        <p>后台APP</p>
                        <p>下载地址</p>
                    </div>
                    <div class="download-open">
                        <span @click="handleCopy(address.managerApp)">复制</span>
                        <span @click="handleJump(address.managerApp)">打开</span>
                    </div>
                </div>
            </div>
            <!-- <div class="con-info info-3">
                <div class="info-item">
                    <img :src="address.h5" />
                </div>
                <div class="info-item">
                    <span>h5网页版</span>
                    <span @click="handleSaveImg">保存</span>
                </div>
            </div> -->
        </div>
        <div class="content">
            <div class="count-item">
                <div class="count-title">
                    <img src="../../assets/home/count_n.png" alt title/>
                    <span>玩家余额合计</span>
                </div>
                <div class="count-info">
                    <span :title="sysInfo.totalBalanceOfAccount">{{sysInfo.totalBalanceOfAccount}}</span>
                </div>
            </div>
            <div class="count-item">
                <div class="count-title">
                    <img src="../../assets/home/count_m.png" alt title/>
                    <span>今日盈亏合计</span>
                </div>
                <div class="count-info">
                    <span>{{sysInfo.totalProfitAndLoss}}</span>
                </div>
            </div>
            <div class="count-item">
                <div class="count-title">
                    <img src="../../assets/home/count_s.png" alt title/>
                    <span>玩家总数/当前在线</span>
                </div>
                <div class="count-info">
                    <span>{{allPerson}}</span>
                    <span>/</span>
                    <span>{{onlinePerson}}</span>
                </div>
            </div>
        </div>
        <div class="content" style="flex-direction: column">
            <div class="list-header">
                <div class="list-title">
                    <span>上下分管理</span>
                    <Checkbox class="check" v-model="voiceStatus" @on-change="changeVoice">上下分消息声音提示</Checkbox>
                    <div class="list-info">
                        <span>今日上分合计：</span>
                        <span>{{upperScoreAll}}</span>
                        <span>今日下分合计：</span>
                        <span>{{lowerScoreAll}}</span>
                        <a href="#" @click="handleJumpRecord">上下分历史记录>></a>
                    </div>
                </div>
            </div>
            <div class="list-con">
                <div class="list-item" v-for="(item, index) in scoreData" :key="index">
                    <div class="item-info header">
                        <img :src="item.header" alt title/>
                    </div>
                    <div :class="['item-info', 'username', item.type === 1 ? 'active' : 'no-active']">
                        <p>用户名</p>
                        <p>{{item.loginName}}</p>
                    </div>
                    <div :class="['item-info', item.type === 1 ? 'active' : 'no-active']">
                        <p>ID</p>
                        <p>{{item.showId}}</p>
                    </div>
                    <div :class="['item-info', item.type === 1 ? 'active' : 'no-active']">
                        <p>当前余额</p>
                        <p>{{item.balance}}</p>
                    </div>
                    <div :class="['item-info', item.type === 1 ? 'active' : 'no-active']">
                        <p>申请时间</p>
                        <p>{{item.time}}</p>
                    </div>
                    <div :class="['item-info', 'status', 'money-' + item.type, item.type === 1 ? 'active' : 'no-active']">
                        <p>上/下分请求</p>
                        <p>{{item.management === 3 ? item.money : item.status}}</p>
                    </div>
                    <div class="item-info operate">
                        <span v-show="item.management === 3" @click="handleOperate(item, true)">同意</span>
                        <span v-show="item.management === 3" @click="handleOperate(item, false)">拒绝</span>
                    </div>
                </div>
            </div>
        </div>
        <Modal v-model="agree.show" class-name="vertical-center-modal" width="300">
            <p slot="header" style="color: #0d1941;font-size: 16px;text-align:center">
                <span>{{agree.method ? '同意操作' : '拒绝操作'}}</span>
            </p>
            <p style="text-align:center">{{agree.method ? '是否同意操作?' : '是否拒绝操作?'}}</p>
            <div slot="footer" class="modal-footer">
                <div class="enter-cancel" @click="agree.show = false">取消</div>
                <div class="enter-ok" @click="handleEnterCancel">确定</div>
            </div>
        </Modal>
        <a style="display: none" :href="address.h5" download="" ref="saveImg"></a>
    </div>
</template>

<script>
import Bus from '../../tools/eventBus/index.js'

export default {
    data() {
        const that = this;
        return {
            address: {
                app: '',
                managerApp: '',
                h5: ''
            },
            agree: {
                show: false,
                method: false,
                item: {}
            },
            voiceStatus: true,
            roomNumber: '',
            safetyCode: '',
            appOpenOrClose: [
                {
                    status: false,
                    openTitle: '赛车开启',
                    closeTitle: '赛车关闭',
                    show: false,
                    id: '',
                    type: 1
                },
                {
                    status: false,
                    openTitle: '飞艇开启',
                    closeTitle: '飞艇关闭',
                    show: false,
                    id: '',
                    type: 2
                },
                {
                    status: false,
                    openTitle: '极速开启',
                    closeTitle: '极速关闭',
                    show: false,
                    id: '',
                    type: 4
                },
                {
                    status: false,
                    openTitle: '时时彩开启',
                    closeTitle: '时时彩关闭',
                    show: false,
                    id: '',
                    type: 3
                },
                {
                    status: false,
                    openTitle: '澳10开启',
                    closeTitle: '澳10关闭',
                    show: false,
                    id: '',
                    type: 5
                },
                {
                    status: false,
                    openTitle: '澳5开启',
                    closeTitle: '澳5关闭',
                    show: false,
                    id: '',
                    type: 6
                },
                {
                    status: false,
                    openTitle: '红包开启',
                    closeTitle: '红包关闭',
                    show: false,
                    id: '',
                    type: 7
                },
                {
                    status: false,
                    openTitle: 'AI计划开启',
                    closeTitle: 'AI计划关闭',
                    show: false,
                    id: '',
                    type: 8
                }
            ],
            scoreData: [],
            isLoading: false,
            isLoadingPoptip: false,
            lowerScoreAll: 0,
            upperScoreAll: 0,
            poptipWav: require('../../assets/audio/4204.wav'),
            poptipMp3: require('../../assets/audio/4204.mp3'),
            interval: null,
            isLoadingRoom: false,
            sysInfo: {
                totalBalanceOfAccount: 0,
                totalProfitAndLoss: 0
            },
            allPerson: 0,
            onlinePerson: 0,
            auto1: null,
            isAgree: false
        }
    },
    beforeRouteEnter (to, from, next) {
        // ...
        next(vm => {
            vm.safetyCode = localStorage.getItem('safetyCode');
            vm.roomNumber = localStorage.getItem('roomNum');
            let voiceStatus = localStorage.getItem('voiceStatus');
            if (voiceStatus)
                vm.voiceStatus = voiceStatus == 1 ? true : false;
            let h5address = vm.getLocalData('h5address');
            if (h5address) {
                vm.address.h5 = h5address;
            }
            vm.getApplyData();

            vm.auto1 = function() {
                vm.handleGetInfo().then(function() {
                    vm.handleGetPerson().then(function() {
                        vm.handleGetPlatformInfo();
                        setTimeout(function() {
                            if (vm.auto1)
                                vm.auto1();
                        }, 30000)
                    })
                })
            }
            vm.auto1();
        })
    },
    beforeRouteLeave (to, from, next) {
        // ...
        let that = this;

        that.auto1 = null;
        // that.interval = null;
        next();
    },
    mounted() {
        let that = this;

        Bus.$on('upperBranch', function(data) {
            that.scoreData = data.map(item => {
                return {
                    header: item.Avatar ? (item.Avatar.indexOf('://') === -1 ? ('/' + item.Avatar) : item.Avatar) : '',
                    loginName: item.LoginName,
                    id: item.ID,
                    showId: item.OnlyCode,
                    money: item.Amount,
                    time: item.ApplyTime,
                    status: item.Message,
                    management: item.Management,
                    type: item.ChangeType,
                    balance: item.UserMoney
                }
            })
        })
        that.handleRoomInfo();
    },
    methods: {
        handleJumpRecord() {
            let that = this;

            Bus.$emit('changeActive', 'other');
            that.$router.push({name: 'manualUpDown'});
        },
        handleSaveImg(img) {
            let that = this;

            that.$refs['saveImg'].click();
        },
        handleJump(href) {
            window.open(href, '');
        },
        changeVoice(bool) {
            localStorage.setItem('voiceStatus', bool ? 1 : 2);
            Bus.$emit('voiceStatus');
        },
        handleGetPlatformInfo() {
            let that = this;

            that.$axios({
                url: '/api/Merchant/GetConfiguration'
            }).then(function(data){
                if (data.data.Status === 100) {
                    const result = data.data.Model;
                    that.address.managerApp = result.BackApp;
                    that.address.app = result.GameApp;
                    that.address.h5 = result.H5Url;
                    if (result.H5Url) {
                        that.setLocalData('h5address', result.H5Url);
                    }
                }
            })
        },
        handleGetInfo() {
            let that = this;
            return new Promise((resolve, reject) => {
                that.$axios({
                    url: '/api/Merchant/GetMerchantInfos'
                }).then(data => {
                    if (data.data.Status === 100) {
                        const result = data.data.Model;

                        that.sysInfo.totalProfitAndLoss = result.ProLoss;
                        that.sysInfo.totalBalanceOfAccount = result.AllAmount;
                    }
                    resolve();
                })
            })
        },
        handleGetPerson() {
            let that = this;
            return new Promise(function(resolve, reject) {
                that.$axios({
                    url: '/api/Merchant/GetAllAndOnline'
                }).then(data => {
                    if (data.data.Status === 100) {
                        const result = data.data.Model;

                        that.allPerson = result.AllCount;
                        that.onlinePerson = result.OnlineCount;
                    }
                    resolve();
                })
            })
        },
        handleRoomInfo() {
            return new Promise((resolve, reject) => {
                let that= this;

                if (!that.isLoadingRoom) {
                    that.isLoadingRoom = true;

                    that.$axios({
                        url: '/api/Room/GetRoomNum'
                    }).then(data => {
                        that.isLoadingRoom = false;
                        if (data.data.Status === 100) {
                            localStorage.setItem('roomNum', data.data.Model);
                            that.roomNumber = data.data.Model;
                        }
                        resolve();
                    })
                }
            })
        },
        handleCopy(content) {
            let that = this;
            const textarea = document.createElement("textarea");
            textarea.style.position = "absolute";
            textarea.style.left = "-9999px";
            textarea.style.top = "-9999px";
            textarea.setAttribute("readonly", ""),
            textarea.value = content,
            document.body.appendChild(textarea);
            textarea.select();
            document.execCommand("Copy") ? that.$Message.success('复制成功') : that.$Message.error('复制失败');
        },
        handleChangeStatus(item) {
            let that = this;

            if (!that.isLoading) {
                that.isLoading = true;

                that.$axios({
                    url: '/api/Room/SwitchGames',
                    params: {
                        gameID: item.id,
                        type: item.type
                    }
                }).then(function(data) {
                    if (data.data.Status === 100) {
                        that.$Message.success(data.data.Message);
                    } else {
                        that.$Message.error(data.data.Message);
                    }
                    that.isLoading = false;
                    that.getGameInfoData();
                })
            }
        },
        handleOperate(item, method) {
            let that = this;

            that.agree.item = item;
            that.agree.method = method;
            that.agree.show = true;
        },
        handleEnterCancel() {
            let that = this;

            if (that.agree.method) {
                that.handleAgree();
            } else {
                that.handleRefuse();
            }
        },
        handleAgree() {
            let that = this;

            if (!that.isAgree) {
                that.isAgree = true;
                that.agree.show = false;
                that.$axios({
                    url: '/api/Merchant/AgreeOperate',
                    method: 'post',
                    params: {
                        id: that.agree.item.id
                    }
                }).then(function(data) {
                    that.isAgree = false;
                    if (data.data.Status === 100) {
                        that.$Message.success(data.data.Message);
                        that.getApplyData();
                        that.getTotalScore();
                    }
                })
            }
        },
        handleRefuse(item) {
            let that = this;

            if (!that.isLoading) {
                that.isLoading = true;
                that.agree.show = false;
                that.$axios({
                    url: '/api/Merchant/RefuseOperate',
                    method: 'post',
                    params: {
                        id: that.agree.item.id
                    }
                }).then(function(data) {
                    if (data.data.Status === 100) {
                        that.$Message.success(data.data.Message);
                        that.getApplyData();
                        that.getTotalScore();
                    }
                    that.isLoading = false;
                })
            }
        },
        getGameInfoData() {
            let that = this;

            if (!that.isLoading) {
                that.isLoading = true;

                that.$axios({
                    url: '/api/Room/GetGameIDs'
                }).then(function(data) {
                    if (data.data.Status === 100) {
                        const result = data.data.Data;

                        that.appOpenOrClose.forEach(function(item) {
                            let opt = result.find(opt => {
                                return opt.Type === item.type
                            });

                            if (opt) {
                                item.id = opt.ID;
                                item.show = true;
                                item.status = opt.Status === 1;
                            } else {
                                item.show = false;
                            }
                        })
                    }
                    that.isLoading = false;
                })
            }
        },
        getApplyData() {
            return new Promise((resolve, reject) => {
                let that = this;

                if (!that.isLoadingPoptip) {
                    that.isLoadingPoptip = true;

                    that.$axios({
                        url: '/api/Merchant/GetUserApply'
                    }).then(function(data) {
                        if (data.data.Status === 100) {
                            const result = data.data.Data;

                            that.scoreData = result.filter(item => item.Management === 3 && item.Status === '正常').map(item => {
                                return {
                                    header: item.Avatar ? (item.Avatar.indexOf('://') === -1 ? ('/' + item.Avatar) : item.Avatar) : '',
                                    loginName: item.LoginName,
                                    id: item.ID,
                                    showId: item.OnlyCode,
                                    money: item.Amount,
                                    time: item.ApplyTime,
                                    status: item.Message,
                                    management: item.Management,
                                    type: item.ChangeType,
                                    balance: item.UserMoney
                                }
                            })

                            if (that.voiceStatus) {
                                let status = that.scoreData.some(item => item.management === 3);

                                if (status) {
                                    let au = document.createElement('audio');
                                    au.preload = 'auto';
                                    au.src = that.poptipWav;
                                    au.src = that.poptipMp3;
                                    au.play();
                                }
                            }
                            that.agree.show = false;
                            that.isAgree = false;
                        }
                        that.isLoadingPoptip = false;
                        resolve();
                    }).catch(function(e) {
                        resolve(e)
                    })
                }
            })
        },
        getTotalScore() {
            let that = this;

            that.$axios({
                url: '/api/Merchant/GetTotalScore'
            }).then(function(data) {
                if (data.data.Status === 100) {
                    const res = data.data.Model;
                    that.lowerScoreAll = res.Low;
                    that.upperScoreAll = res.Up;
                }
            })
        },
    },
}
</script>

